.compte{
    height: 100px;
    width: 250px;
    position: fixed;
    z-index: 1;
    bottom: 0;
    overflow-x: hidden;
    padding-bottom: 10px;
    margin-bottom: 100px;
    display:flex;
    margin-left: 20px;
    justify-content: space-around;
    align-items: center;
}



.compteText{
    color: var(--white);
    font-size: 30px;
}

.compteText:hover{
    color: var(--red);
}

